import React, { useState } from "react";
import InputText from "../inputs/InputText";
import Button from "@material-ui/core/Button";

export default function CreateGroupOfStudents() {
  const [groupNum, setGroupNum] = useState("");
  const [prepVector, setPrepVector] = useState("");
  const submit = () => {
    return {
      groupNum,
      prepVector,
    };
  };
  return (
    <form onSubmit={submit}>
      <div className="card admin__card">
        <b className="card__header margin-bottom-medium">
          –°–æ–∑–¥–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã —Å—Ç—É–¥–µ–Ω—Ç–æ–≤
        </b>
        <InputText
          value={groupNum}
          onChange={(e) => {
            setGroupNum(e.target.value);
          }}
          desc="–ù–æ–º–µ—Ä –≥—Ä—É–ø–ø—ã"
          placeholder="–ù–∞—á–Ω–∏—Ç–µ –≤–≤–æ–¥–∏—Ç—å —Ç–µ–∫—Å—Ç..."
        />
        <InputText
          value={prepVector}
          onChange={(e) => {
            setPrepVector(e.target.value);
          }}
          desc="–ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∏"
          placeholder="–ù–∞—á–Ω–∏—Ç–µ –≤–≤–æ–¥–∏—Ç—å —Ç–µ–∫—Å—Ç..."
        />
        <Button variant="contained">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</Button>
      </div>
    </form>
  );
}
