import React from "react";
// import Autocomplete from "@material-ui/core";
import Autocomplete from '@material-ui/lab/useAutocomplete'


const professor = [
  {
    name: "–ú—É—Ä–∞—Ç–æ–≤ –ú—É—Å—Ç–∞—Ñ–∞ –ê–±–¥—É—Ä–µ—à–∏—Ç–æ–≤–∏—á",
  },
  {
    name: "–ö–æ–∑–ª–æ–≤ –ê–ª–µ–∫—Å–∞–Ω–¥—Ä –ò–≤–∞–Ω–æ–≤–∏—á",
  },
  {
    name: "–ë–µ–ª–æ–∑—É–± –í–ª–∞–¥–∏–º–∏—Ä –ê–Ω—Ç–æ–Ω–æ–≤–∏—á",
  },
  {
    name: "–°—Ç–∞—Ä–∫–æ–≤ –ü–∞–≤–µ–ª –ê–ª–µ–∫—Å–∞–Ω–¥—Ä–æ–≤–∏—á",
  },
];

const inputStyle = {
  width: "100%",
  // position: "absolute",
  // top: "0",
  // left: "0",
  // right: "0",
  height: "2.5rem",
  fontSize: "1.2rem",
  border: "none",
  outline: "none",
  padding: "5px",
  backgroundColor: "$primary-gray",
  borderBottom: "1px solid #ddd",
};

export default function SearchPanel() {
  return (
    <Autocomplete
      options={professor}
      getOptionLabel={(option) => option.name}
      renderInput={(params) => (
        <div className="searchPanel" ref={params.InputProps.ref}>
          <input
            // className="search-input"
            style={inputStyle}
            type="text"
            autoFocus="true"
            placeholder="–ù–∞—á–Ω–∏—Ç–µ –≤–≤–æ–¥–∏—Ç—å —Ç–µ–∫—Å—Ç..."
            {...params.inputProps}
          />
        </div>
      )}
    />
  );
}
